import * as $ from 'jquery';


$( document ).ready(function() {

// show uploaded image name value
$('.custom-file-input').on('change', function() {
   let fileName = $(this).val().split('\\').pop();
   $(this).next('.custom-file-label').addClass("selected").html(fileName);
}); 

 

/* ------------- Trigger to hide the underline effect for the Msg, Email, Name titles ---------- */

	let userName = document.querySelector('.user_name');

// when clicking the body, hide line effect

 $(document).on('click', function (event) {

 	// if unregistered UI , add styles
 	if(userName.innerHTML === 'Login') {
  if (!$(event.target).closest('.unreg_name_input, .unreg_email_input, .unreg_msg_textarea').length) {

  			// when clicking out of the page, hide the line slider 
    let unreg_name_lineSlider = document.querySelector('.unreg_name_lineSlider span');
	let unreg_email_lineSlider = document.querySelector('.unreg_email_lineSlider span');
	let unreg_msg_lineSlider = document.querySelector('.unreg_msg_lineSlider span');
		

					unreg_name_lineSlider.style.marginLeft = '-370px';
					unreg_email_lineSlider.style.marginLeft = '-370px';
					unreg_msg_lineSlider.style.marginLeft = '-370px';
      }
    } 
});


 $(document).on('click', function (event) {
let reg_msg_lineSlider = document.querySelector('.reg_msg_lineSlider span');
let userName = document.querySelector('.user_name');

    if(userName.innerHTML !== 'Login') {
    	if (!$(event.target).closest('.reg_user_textarea').length) {
    	
    			reg_msg_lineSlider.style.marginLeft = '-320px';
    	}
    }

});


// navbar toggle hamburger button
document.querySelector('.navbar-toggler').addEventListener('click', function() {
  document.querySelector('.animated-icon1').classList.toggle('open');
});

 

});


 
