editCom(e) {
	 let editPostContainer = document.querySelector('.edit_post_container');
   let editPostWrapper = document.querySelector('.edit_post_wrap');
	 // when clicking of the edit icon, transfer the value of the selected input
	 // inside of the editable content textarea to be edited and saved the new comm

	 // set the key of the edited post in the editPostKey state
   this.setState({ editPostValue: e.message, editPostKey: e.key})
   editPostContainer.style.display = 'block';
   setTimeout(function() {
   	editPostWrapper.style.top = '130px';
   },500);

}

cancelEditPost() {

   let editPostContainer = document.querySelector('.edit_post_container');
   let editPostWrapper = document.querySelector('.edit_post_wrap');

 	// Hide the editor box
   editPostWrapper.style.top = '-200px';

   // hide the backdrop bg 
   setTimeout(function() {
   	  editPostContainer.style.display = 'none';
   },500);
}

onChangeEditPostValue(e) {
	// update the new edited text
	this.setState({ editPostValue: e.target.value})
	 
}

updateEditedPost(e) {
   let editPostContainer = document.querySelector('.edit_post_container'),
   	   editPostWrapper = document.querySelector('.edit_post_wrap'),
   	   editPostTextarea = document.querySelector('.edit_post_textarea'),
   	   editPostLoadingRing = document.querySelector('.lds_ring_editPost');

   // get the time when the message is submitted
	let today = new Date(),
	date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate(),
	time = today.getHours() + ":" + today.getMinutes(),
	lastEdited = date+' '+time;

	// opacity 0.5 for textarea after clicking the change button
	editPostTextarea.style.opacity = '0.5';
	// display the loading ring
	editPostLoadingRing.style.display = 'block';
	// update selected post state with the new message
	this.state.fetchedData.map((item,index) => item.key === this.state.editPostKey ?
			item.message = this.state.editPostValue : false)

	// update the message after was edited
	firebase.database().ref('messages/' + this.state.editPostKey).update({
		message: this.state.editPostValue,
		lastEdited: lastEdited
	}).then(() => 				  
		setTimeout(function() {
		// hide the editpost box
	   editPostWrapper.style.top = '-200px';
		},1000),

       setTimeout(function() {
       	   // hide the backdrop bg after msg was changed
   	     editPostContainer.style.display = 'none';
   	     // opacity 1 for textarea after the message was changed
		editPostTextarea.style.opacity = '1';
		// hide the loading ring
		editPostLoadingRing.style.display = 'none';
       },2000),
       	// clear the editedpost value and editedpost key after change
		this.setState({
			editPostValue: '',
			editPostKey: ''
		}),

	)
      .catch((err) => console.log('error:' + err))

}

openDeleteBox(e) {
	let deletePostContainer = document.querySelector('.confirmDelete');
	let confirmDeleteBox = document.querySelector('.confirmDeleteBox');


	deletePostContainer.style.display = 'block';
	setTimeout(function() {
		deletePostContainer.style.opacity = '1';
	},200);
			setTimeout(function() {
			confirmDeleteBox.style.top = '100px';	
		},500);

			// get the post key, and give the value key to deletePostkey to be deleted if is confirmed
	this.setState({
		deletePostKey: e.key
	})
	
}

closeConfirmDelBox() {
	let deletePostContainer = document.querySelector('.confirmDelete');
	let confirmDeleteBox = document.querySelector('.confirmDeleteBox');

		// hide confirm box container and add some delay
		confirmDeleteBox.style.top = '-200px';
	setTimeout(function() {
		deletePostContainer.style.opacity = '0';
	},200);
	setTimeout(function() {
		deletePostContainer.style.display = 'none';	
	},500);

}

confirmDelete() {
	let deletePostContainer = document.querySelector('.confirmDelete');
	let confirmDeleteBox = document.querySelector('.confirmDeleteBox');

		// hide the confirm container much faster 
		confirmDeleteBox.style.top = '-200px';
	setTimeout(function() {
		deletePostContainer.style.opacity = '0';
	},100);
	setTimeout(function() {
		deletePostContainer.style.display = 'none';	
	},300);

	// call and delete post from database
    this.deleteCom();
}

deleteCom() {
	let selectedPostkey = this.state.deletePostKey;

	// in the messages, search for the selected key and delete it
	firebase.database().ref('messages').child(selectedPostkey).remove()
			.then(() => console.log('Message deleted successfully'),
				// decrease number of user posts if a message is deleted
				this.setState({current_user_postsNr: this.state.current_user_postsNr - 1}))
			.catch((err) => console.log('Something went wrong while deleting the selected message'))


	let fetchedData = [...this.state.fetchedData];

	let ind;

	// find the post with the selected for delete key
    for(let i=0; i<fetchedData.length; i++) {
    	if(fetchedData[i].key === selectedPostkey) {
    		ind = fetchedData[i];
    	}
    }

    // get the index of the selected post
    let find = fetchedData.indexOf(ind);
    // remove the post using the index
    fetchedData.splice(find,1);

    // display the new list after deleting the selected post
    this.setState({ fetchedData: fetchedData})
    // if there are no more comments, reload page.
    if(this.state.fetchedData.length === 1 ) {
    	window.location.reload();
    }

}

