cpUploadUserPhoto() {

	let userCpLoaded = document.querySelector('.user_cp_loader');
			// if there is an image and the image type is correct 
if(this.state.image !== null && this.state.notAnImage !== true) {
				const {image} = this.state;
				// on storage page, inside images directory, put (upload) the new image 
				const uploadTask = storage.ref(`img/${image.name}`).put(image);
	uploadTask.on('state_changed', 
		(snapshot) => {
		userCpLoaded.style.opacity = '1';
		},
		(error)    => {
		 this.setState({ hasError: true});
		},
		() 		   => {
			storage.ref('img')
			.child(image.name)
			.getDownloadURL()
			.then(url => {
				// hide loading bar
				userCpLoaded.style.opacity = '0';
				// after image was uploaded, set the signup_photoUrl state to the uploaded image address
		 this.setState({cp_new_userImage: url, image: null});
				 // call function to update user profile
		 		this.cpUpdateProfilePhoto();
			})
		});
	}
}

cpUpdateProfilePhoto() {
	// update the user profile photo
	let fetchedData = [...this.state.fetchedData];
	let currentUserName = this.state.user.displayName;
	let user = firebase.auth().currentUser;
	// if there is a photo uploaded do
		user.updateProfile({
              photoURL: this.state.cp_new_userImage
				})

	// search for the username inside the fetchedState and change the image with the new one
for(let i=0;  i<fetchedData.length; i++) {
	if(fetchedData[i].name === currentUserName ) {
		fetchedData[i].url = this.state.cp_new_userImage;
	}
}
 
// update photoURL inside state user
let newPhotoURL = Object.assign({}, this.state.user);
newPhotoURL.photoURL = this.state.cp_new_userImage;

	this.setState({ user: newPhotoURL, fetchedData: fetchedData })

}


onChangeSearch(e) {
		let fetchedData = [...this.state.fetchedData];
		let fetchedDataCopy = this.state.fetchedDataCopy;

if(e.target.value.length !== 0) {
	let query = e.target.value.toLowerCase();
		document.querySelector('.noDataToShow').style.display = 'none';
		this.setState({ searchQuery: query, fetchedData: fetchedDataCopy})
} else if(e.target.value.length === 0) {
		document.querySelector('.noDataToShow').style.display = 'none';
		this.setState({ fetchedData: this.state.fetchedDataCopy, searchQuery: ''})

	}  
}

searchOnPage(e)  {
	e.preventDefault();	 

	let fetchedData = [...this.state.fetchedData];
	let fetchedDataCopy = this.state.fetchedDataCopy;
	let loadingSearchRing = document.querySelector('.loading_search_ring');

if(this.state.searchQuery.length !== 0) {
	let searchFor = fetchedDataCopy.filter((item) => item.message.toLowerCase().includes(this.state.searchQuery));
	// display loading ring while searching
	loadingSearchRing.style.display = 'block';
	// hide 'no data to show' message
	document.querySelector('.noDataToShow').style.display = 'none';
	// if there is data to display, hide loading ring and display searched data
	if(searchFor.length !== 0 ) {
	setTimeout(() => {
		this.setState({ fetchedData: searchFor})
		loadingSearchRing.style.display = 'none';
	},1000);
	} else {
		// if there is no data to display, hide loading ring, clear the list and display
			// 'no data to show' message
			loadingSearchRing.style.display = 'none';
				this.setState({ fetchedData: []})
				document.querySelector('.noDataToShow').style.display = 'block';
	}
	
} else {
  	this.setState({ fetchedData: fetchedData})
  }
}

searchOnKeyPress(e) {
	// key listener for navbar search input
	if(e.key === 'Enter') {
     this.searchOnPage(e);
  } else {
    return false;
  }
}

